.job-entry {
    position: relative;
}

.job-entry > h3 {
    position: absolute;
    left: -24px;
    top: -11px;
}

.workpanels {
    z-index: 0;
    position:relative
}

.workpanels::before {
    position: absolute;
    border: 1px black solid;
    content: "";
    width: 1px;
    height: 100%;
    left: 102px;
    z-index: 0;
}

.workpanels .popover {
    display: block;
    top: 50px;
    left: 85px;
    width: calc(100% - 200px);
    max-width: 100%;
}

.workpanels .job-entry .workpanel-marker {
    margin-bottom: 20px;
}

.workpanel-marker {
    background-color: black;
    display: block;
    border-radius: .5rem;
    width: 40px;
    height: 40px;
    margin-left: 55px;
}

.workpanel-marker-google {
	background: -webkit-gradient(
		linear
		, left bottom, left top
		, from(#4285f4)
		, color-stop(25%, #4285f4)
		, color-stop(25%, #34a853)
		, color-stop(50%, #34a853)
		, color-stop(50%, #fbbc05)
		, color-stop(75%, #fbbc05)
		, color-stop(75%, #ea4335)
		, to(#ea4335)
	);
	background: -webkit-linear-gradient(
		bottom
		, #4285f4 0%
		, #4285f4 25%
		, #34a853 25%
		, #34a853 50%
		, #fbbc05 50%
		, #fbbc05 75%
		, #ea4335 75%
		, #ea4335 100%
	);
	background: -o-linear-gradient(
		bottom
		, #4285f4 0%
		, #4285f4 25%
		, #34a853 25%
		, #34a853 50%
		, #fbbc05 50%
		, #fbbc05 75%
		, #ea4335 75%
		, #ea4335 100%
	);
	background: linear-gradient(
		0deg
		, #4285f4 0%
		, #4285f4 25%
		, #34a853 25%
		, #34a853 50%
		, #fbbc05 50%
		, #fbbc05 75%
		, #ea4335 75%
		, #ea4335 100%
	);
}

.workpanel-marker-lyft {
	background: -webkit-gradient(
		linear
		, left bottom, left top
		, from(#f3f3f5)
		, color-stop(50%, #f3f3f5)
		, color-stop(50%, #ff00bf)
		, to(#ff00bf)
	);
	background: -webkit-linear-gradient(
		bottom
		, #f3f3f5 0%
		, #f3f3f5 50%
		, #ff00bf 50%
		, #ff00bf 100%
	);
	background: -o-linear-gradient(
		bottom
		, #f3f3f5 0%
		, #f3f3f5 50%
		, #ff00bf 50%
		, #ff00bf 100%
	);
	background: linear-gradient(
		0deg
		, #f3f3f5 0%
		, #f3f3f5 50%
		, #ff00bf 50%
		, #ff00bf 100%
	);
}

.workpanel-marker-amazon {
	background: -webkit-gradient(
		linear
		, left bottom, left top
		, from(#fefefe)
		, color-stop(33%, #fefefe)
		, color-stop(33%, #ff9900)
		, color-stop(66%, #ff9900)
		, color-stop(66%, #146eb4)
		, to(#146eb4)
	);
	background: -webkit-linear-gradient(
		bottom
		, #fefefe 0%
		, #fefefe 33%
		, #ff9900 33%
		, #ff9900 66%
		, #146eb4 66%
		, #146eb4 100%
	);
	background: -o-linear-gradient(
		bottom
		, #fefefe 0%
		, #fefefe 33%
		, #ff9900 33%
		, #ff9900 66%
		, #146eb4 66%
		, #146eb4 100%
	);
	background: linear-gradient(
		0deg
		, #fefefe 0%
		, #fefefe 33%
		, #ff9900 33%
		, #ff9900 66%
		, #146eb4 66%
		, #146eb4 100%
	);
}

.workpanel-marker-vertafore {
	background: -webkit-gradient(
		linear
		, left bottom, left top
		, from(#f95203)
		, color-stop(50%, #f95203)
		, color-stop(50%, #fefefe)
		, to(#fefefe)
	);
	background: -webkit-linear-gradient(
		bottom
		, #f95203 0%
		, #f95203 50%
		, #fefefe 50%
		, #fefefe 100%
	);
	background: -o-linear-gradient(
		bottom
		, #f95203 0%
		, #f95203 50%
		, #fefefe 50%
		, #fefefe 100%
	);
	background: linear-gradient(
		0deg
		, #f95203 0%
		, #f95203 50%
		, #fefefe 50%
		, #fefefe 100%
	);

}

.workpanel-marker-tech {
	background: -webkit-gradient(
		linear
		, left bottom, left top
		, from(#FFFFFF)
		, color-stop(50%, #FFFFFF)
		, color-stop(50%, #EAAA00)
		, to(#EAAA00)
	);
	background: -webkit-linear-gradient(
		bottom
		, #FFFFFF 0%
		, #FFFFFF 50%
		, #EAAA00 50%
		, #EAAA00 100%
	);
	background: -o-linear-gradient(
		bottom
		, #FFFFFF 0%
		, #FFFFFF 50%
		, #EAAA00 50%
		, #EAAA00 100%
	);
	background: linear-gradient(
		0deg
		, #FFFFFF 0%
		, #FFFFFF 50%
		, #EAAA00 50%
		, #EAAA00 100%
	);

}

.workpanels .years {
    width: 55px;
    display:block;
    margin-top: -20px;
}

.workpanels .years .entry:not(:last-child) {
    margin-bottom: 95px;
}

.workpanels .entries {
    padding-left: 28px;
    z-index: 0;
}

.workpanels .timeline-entry {
    background-color: #f4f4f4;
    display: block;
    border-radius: .5rem;
    left: 107px;
    padding: 5px 20px 10px 20px;
    position: relative;
    top: -60px;
    width: calc(100% - 150px);
}

.workpanels-content {
    margin-left: 50px;
}



